body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header{
    background: #333;
    padding: 10px 0;
    position: fixed;
    width: 100%;
    height: 50px;
    z-index: 99;
}

header .logo {
    color: #fff;
    font-size: 20px;
    font-weight: bold;
}
.cart{
    color: #fff;
    cursor: pointer;
}

.cart-container{
    background-color: #fff;
    width: 100vw;
    right: -101vw;
    margin-top: 50px;
    height: 94vh;
    position: fixed;
    z-index: 100;
    -webkit-transition: ease 1s;
    transition: ease 1s;
}

.cart-container.active{
    right: 0;
}
.cart-img img{
    width: 50px;

}
.main{
    min-height: 600px;
}

.nav-left{
    margin-top: 50px;
    text-align: left;
}

.left-bar ul{
    list-style-type: none;
    padding:0;
    border-top: 1px solid #c7c8ca;
    border-bottom: 1px solid #c7c8ca;
    width: 60%;
    padding-bottom: 20px;
    padding-top: 20px;
}
.left-bar nav li{
    padding: 3px 3px;
    cursor: pointer;
}
.col{
    display: inline-block;
}
.product-main img{
    width: 100%;
    max-height: 350px;
}
.btn.btn-link{
    border: 1px solid #007bff;
} 
.banner{
    padding-top: 50px;
}

.banner img{
    height: 200px;
}

.breadcrumb-banner{
    padding: 30px 10px 10px 0px;
}

.breadcrumb-banner span{
    font-size: 23px;
}


